import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';

import { College } from './entity';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};
@Injectable({
  providedIn: 'root'
})
export class SchoolService {

  private collegeUrl = '../school/';//去掉list.ctl
  constructor(private http: HttpClient) { }

  /** GET heroes from the server */
  getColleges(): Observable<College[]> {
    return this.http.get<any>(this.collegeUrl + 'list.ctl')
      .pipe(
         catchError(this.handleError<College[]>('getColleges', []))
      );
  }
  /**
   * Handle Http operation that failed.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error); // log to console instead
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
  /** DELETE: delete the college from the server */
  deleteCollege(college: College | number): Observable<College> {
    const id = typeof college === 'number' ? college : college.id;
    return this.http.get<any>(this.collegeUrl + 'delete.ctl?id=' + id, httpOptions).pipe(
      catchError(this.handleError<College>('deleteCollege'))
    );
  }
  addCollege(college: { no: string; description: string; remarks: string }): Observable<College> {
    return this.http.post<College>(this.collegeUrl + 'add.ctl', college, httpOptions).pipe(
      catchError(this.handleError<College>('addCollege'))
    );
  }



}

