import { Component, OnInit } from '@angular/core';
import { College } from '../entity';
import { SchoolService } from '../school.service';

@Component({
  selector: 'app-school',
  templateUrl: './school.component.html',
  styleUrls: ['./school.component.css']
})
export class SchoolComponent implements OnInit {
  colleges: College[];
  constructor(private schoolService: SchoolService) { }

  ngOnInit() {
    this.getColleges();
  }
  getColleges(): void {
    this.schoolService.getColleges ()
      .subscribe(res => this.colleges = res);
    console.log(this.colleges);
  }
  delete(college: College| number): void {//两种类型的数据都可以传进来
    this.colleges = this.colleges.filter(h => h !== college);//先做前端删除
    this.schoolService.deleteCollege(college).subscribe();
  }
}
