import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

import { SchoolService } from '../school.service';


@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.css']
})
export class AddComponent implements OnInit {
  @Input() college: { no: string; description: string; remarks: string };
  constructor(
    private route: ActivatedRoute,
    private schoolService: SchoolService,
    private location: Location
  ) {}

  ngOnInit(): void {}

  save(description: string, no: string, remarks: string): void { //多个文本框数据
    this.college = {no: no.trim(), description: description.trim(), remarks: remarks.trim()};
    if (!description) { return; }
    this.schoolService.addCollege(this.college)
      .subscribe(() => this.goBack());
  }

  goBack(): void {
    this.location.back();
  }
}
